* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}
a {
  color: #333333;
  text-decoration: none;
}
.container {
  padding: 0 1rem;
}
.btn {
  display: inline-block;
  border: none;
  background: #555555;
  color: #ffffff;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
.btn:hover {
  background: #777777;
}
